.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.container{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #4b82f1b6;
}

.table-wrapper{
  background-color: #f5f5f5;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

h2{
  text-align: center;
  margin-bottom: 20px;
}

input{
  width: 88%;
  padding: 10px;
  margin-bottom: 20px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

table{
  width: 100%;
  border-collapse: collapse;
}

td{
  padding: 10px;
}
tr:nth-child(odd){
  background-color: #f9f9f9;
}
tr:hover{
  background-color: #e9e9e9;
}

marquee{
  padding: 1px;
  font-size: medium;
  font-weight: bold;
  margin-top: 20px;
  font-family:cursive;
}